@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@200&display=swap');

body {
  margin: 0;
  font-family: 'Barlow', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding-bottom: 30px;
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.titulo {
  text-align: center;
  color: rgba(53, 62, 82, 0.9);
  font-weight: bold;
  font-family: 'Barlow', sans-serif;
  text-shadow: 2px 2px 2px rgba(150, 150, 150, 0.71);}

.boxTitulo {
  margin: auto;
  width: 100%;
  background-color: rgba(31, 185, 149, 0.3); 
  margin-top: 35px;
  padding: 2px;
  box-shadow: 0px 6px 12px -5px rgba(63,63,64,0.48);

}

.clicavel {
  cursor:pointer;
}

